                <div class="source" slot="source"><template>
  <bee-table :data="data" :height="200" @selectionChange="selectionChange" ref="selectionTable">
    <bee-table-column width="40" fixed="left" type="selection"></bee-table-column>
    <bee-table-column width="160" label="" prop="name"></bee-table-column>
    <bee-table-column width="160" label="" prop="age"></bee-table-column>
    <bee-table-column width="160" label="" prop="grade"></bee-table-column>
    <bee-table-column width="160" label="" prop="subject"></bee-table-column>
    <bee-table-column fixed="right" width="260" label="">
      <template slot-scope="scope">
        <span class="primary" @click="view(scope.row, scope.$index)"></span>
        <span class="primary"></span>
      </template>
    </bee-table-column>
  </bee-table>

  <bee-button @click="clearAllSelected"></bee-button>
  <bee-button @click="setSelected(0, 1)">1, 2</bee-button>
</template>

</div>
                
