                <div class="source" slot="source"><template>
  <bee-table :data="data" :height="200">
    <bee-table-column width="40">
      <template slot="header" slot-scope="scope">#</template>
      <template slot-scope="scope">
        {{(scope.$index + 1) * 2}}
      </template>
    </bee-table-column>
    <bee-table-column width="160" label="" prop="name">
      <template slot="header" slot-scope="scope">
        <bee-icon class="table-header-icon" icon="info"></bee-icon>
      </template>
    </bee-table-column>
    <bee-table-column width="160" prop="age">
      <template slot="header" slot-scope="scope">age</template>
    </bee-table-column>
    <bee-table-column width="160" label="" prop="grade"></bee-table-column>
    <bee-table-column width="160" label="" prop="subject"></bee-table-column>
  </bee-table>
</template>

